import React from 'react'
import { Link } from 'react-router-dom'
import styleprogram from './Program.module.css'
import imgprogram from './img/autism-concept-illustration_114360-5049.jpg'

export default function Program() {
  return <>
  
  <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
    <div className="col-10 col-sm-8 col-lg-6" >
      <img src={imgprogram} className={styleprogram.imgprogram} alt='program autism'/>
    </div>


    <div className="col-lg-6">
      
        <h1 className={styleprogram.programtitle}> </h1>
        <p className={styleprogram.lead} >                                                      ,                                        .
                                                           .</p>
      
      

      <h3 className={styleprogram.programtitle}>  </h3>
      <p className={styleprogram.lead} id="description">                </p>
    </div>
  </div>
  
  
  


  <Link to="/program1"><button className={styleprogram.ready} >  </button></Link>

  <Link to="/practicale"><button className={styleprogram.ready}  >  </button></Link>

 
  
  </>
}
