import React from 'react'
import { Link } from 'react-router-dom'
export default function Nav({userData , logOut}) {
  return <>
  
  <header id="header" className="fixed-top ">
  <nav className="navbar navbar-expand-lg navbar-light ">
  <div className="container">
    <Link className="navbar-brand logo" to=""> <span className='active'></span>.</Link>
    <span className='ourmotto'>" "</span>
    
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
    {userData?
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link " to=''><span className='active'></span></Link>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link " to="#" >
            
            <i className="fas fa-chevron-down inav"></i>
          </Link>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><Link className="dropdown-item" to="articleone"></Link></li>
            <li><Link className="dropdown-item" to="videoone"></Link></li>
          </ul>
        </li>
        
        <li className="nav-item">
          <Link className="nav-link" to="scale"></Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="program"></Link>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link " to="#" >
        
        <i className="fas fa-chevron-down inav"></i>
          </Link>
          <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
            <li><Link className="dropdown-item" to="fun"></Link></li>
            <li><Link className="dropdown-item" to="practicale"></Link></li>
          </ul>
        </li>

        <li className="nav-item">
          <Link className="nav-link" to="#"></Link>
        </li>
      </ul>:''}

      {
      userData? <>
       
      
        <Link onClick={logOut} to='' className="btn btn-outline logouttt" type="submit"> </Link>


            </>:""
           
    }
    
    </div>
  </div>
</nav>
</header>
  </>
}
