import React from 'react'
import {Link} from 'react-router-dom'
import scalestyle from './Scale.module.css'
import imgscale from './img/Cars.jpg'

export default function Scale() {
  return <>
  <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
    <div className="col-10 col-sm-8 col-lg-6">
     <img src={imgscale} alt="carsscale" className={scalestyle.imgscale}/>
    </div>
    <div className="col-lg-6">
      <h1 className={scalestyle.cars}>  2</h1>
      <p className={scalestyle.lead}>                       1988          .   
          
           :
                    .
                    .
                           .
                   .</p>
      <div className="d-grid gap-2 d-md-flex justify-content-md-start mt-4">
        
        <Link to="/startscale"><button type="button" className={scalestyle.ready}> </button></Link>
        
      </div>
    </div>
  </div>
  </>
}
